import { Component, OnInit, ViewChild } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../../shared-ui/global.service';
import { ModalDirective } from "ngx-bootstrap/modal";

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

  @ViewChild("deleteCompanyModal", { static: false })
  public deleteCompanyModal: ModalDirective;
  
  pager: any = {};
  companies: any;
  pagination: any;
  multiSelect: any;
  selectedCompany: any = [];
  currentCompany: any;
  
  constructor(
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
  ) {
    this.globalService.sendActionChildToParent('companies');  
  }

  ngOnInit() {
    this.getCompanies();
  }

  getCompanies(){
    this.spinner.show();
    this.globalService.getCompanies({}).subscribe(data => {
      console.log("data ------------ ", data);
      this.spinner.hide();
      if(Object.keys(data).length){
        this.companies = data.Items;
        this.pagination = data.meta;
      }
    }, error => {
      this.toastr.error('There are some server error! Please check connection.', 'Error');
    });
  }

  showDeleteModal(company: any){
    this.currentCompany = company;
    this.deleteCompanyModal.show();
  }

  multiSelectCompany(multiSelect: any){
    this.selectedCompany = [];
    this.companies.forEach(element => {
      if(multiSelect){
        element.added = false;
      }else{
        element.added = true;
        this.selectedCompany.push(element);
      }
    });
  }
  
  selectCompany(company: any){
    let found = this.selectedCompany.filter(e => e.id == company['id']);
    if(found.length){
      this.selectedCompany.splice(this.selectedCompany.indexOf(found[0]), 1);
    }else{
      this.selectedCompany.push(company);
    }
  }

  deleteCompany(){
    let postData = {
      isActive: 0,
    }
    this.globalService.updateCompany(postData, this.currentCompany['id']).subscribe(data => {
      this.deleteCompanyModal.hide();
    });
  }
  
}
